        /**
         * 
         * @param
         * shopCode  Number      CODE
         * shopClassCode Number  CODE
         * areaCode Number       CODE
         * page Number           
         * rows Number           
         *
         * isFrist  
         **/
        getGoodsByShop(pageNumber, isFrist = true) {
            let leftIndex = this.activeKey;
            let rows = this.rows;
            let paramData = {
                shopCode: this.shopCode,
                shopClassCode: this.leftClassList[leftIndex].shopClassCode,
                areaCode: this.$userInfo.AREA_CODE,
                ownerCode: this.$userInfo.OWNER_CODE,
                page: pageNumber,
                rows: this.rows,
                sortMethod: this.sortMethod
            }
            this.getGoodsForShop(paramData).then(res => {
                if (res.resultCode == "0") {
                    if (res.data.list.length != 0) {
                        res.data.list.map((item, index) => {
                            item.skuShopNum = 0;
                            if (index % 2 == 0) {
                                item.spuPicUrl = item.spuPicUrl.replace(/xwjfile.juhaolian.cn/, 'xwjfilea.juhaolian.cn')
                            }
                            Vue.prototype.$isHasLocalImage(item.spuPicUrl, function (localImgUrl) {
                                item.spuPicUrl = localImgUrl;
                            })
                        })
                    }
                    this.leftClassList[leftIndex].goodsList = res.data.list;
                    this.leftClassList[leftIndex].pageNumber = 2;
                    // this.leftClassList[leftIndex].rows = rows;
                    this.leftClassList[leftIndex].finished = !res.data.hasNextPage;
                    this.goodsList = res.data.list;
                    this.isloading = true;
                    this.loading = false;
                    this.finished = !res.data.hasNextPage;
                    this.isShowBlankImg = false;
                }else
                {
                    Dialog.alert({
                        message: res.msg
                    }).then(() => {
                        // on confirm
                        this.goBack();
                    })
                    this.goodsList = [];
                    this.isloading = true;
                    this.loading = false;
                    this.finished = true;
                }
                if (!isFrist) {
                    // this.$refs.scrollContent.scrollTop = this.$refs.sideBar.offsetTop-46;
                    // this.$el.querySelector('#classifyList').scrollIntoView(true)
                    // this.$el.querySelector('#classifyList').scrollIntoView({block: "start", behavior: "smooth"})
                }
                this.$store.commit("shopGoodsPageNumberChange", {
                    custPageNum: true,
                    pageNumber: 2
                });
            }).catch(error => {
            });
        },
